@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;600&display=swap);
:root{--font-size-root: 62.5%;--color-darkest-rgb: 0, 0, 0;--color-darkest: #000;--color-darker: #333;--color-dark: #666;--color-light: #ddd;--color-lighter: #eee;--color-lightest: #fff;--color-lightest-rgb: 255, 255, 255;--color-border-dark: #1f1f1f;--color-border-light: #f1f1f1;--color-link-dark: blue;--color-link-light: cyan;--font-family-basic: 'Roboto Mono', monospace;--font-size-basic: 1.5rem;--font-weight-basic: 400;--font-weight-title: 600;--line-height-basic: 1.4;--line-height-title: 1.2;--transition-duration-basic: .2s;--indent-basic: 1.6rem;--year-height: 3.6rem;--genre-width: var(--year-height);--bar-height: 4.8rem;--cell-padding: calc(var(--indent-basic) / 2);--cell-background-color-dark: #0a0a0a;--cell-background-color-light: #fafafa;--game-release-unknown-background-color-dark: #0e0e0e;--game-release-unknown-background-color-light: #efefef}*,*::before,*::after{margin:0;padding:0;box-sizing:border-box}html,body{height:100%}html{font-size:62.5%;font-size:var(--font-size-root, 62.5%);-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body{background-color:#fff;background-color:var(--color-lightest);color:#666;color:var(--color-dark);font-family:'Roboto Mono', monospace;font-family:var(--font-family-basic, monospace);font-weight:400;font-weight:var(--font-weight-basic);font-size:1.5rem;font-size:var(--font-size-basic);line-height:1.4;line-height:var(--line-height-basic)}a{color:blue;color:var(--color-link-dark);transition:all .2s;transition:all var(--transition-duration-basic)}a:hover,a.active{text-decoration:none}a.active{cursor:default}h1,h2{font-weight:600;font-weight:var(--font-weight-title);line-height:1.2;line-height:var(--line-height-title)}h1{margin:0}h6{font-size:1.5rem;font-size:var(--font-size-basic);line-height:1.4;line-height:var(--line-height-basic)}ol,ul{padding:0;list-style:none}small{font-size:1rem}

html:not(.js-focus-visible) :focus:not(:focus-visible){outline:0}html:not(.js-focus-visible) :focus-visible,html.js-focus-visible :focus{outline:2px solid currentColor}

.visually-hidden{position:absolute !important;width:1px !important;height:1px !important;padding:0 !important;margin:-1px !important;overflow:hidden !important;clip:rect(0, 0, 0, 0) !important;white-space:nowrap !important;border:0 !important}

#app{display:grid;grid-template-rows:auto 1fr auto;min-height:100%}

