@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;600&display=swap');
@import url('../node_modules/normalize.css/normalize.css');

:root {
    --font-size-root: 62.5%;

    // Colors
    --color-darkest-rgb: 0, 0, 0;
    --color-darkest: #000;
    --color-darker: #333;
    --color-dark: #666;
    --color-light: #ddd;
    --color-lighter: #eee;
    --color-lightest: #fff;
    --color-lightest-rgb: 255, 255, 255;

    --color-border-dark: #1f1f1f;
    --color-border-light: #f1f1f1;

    --color-link-dark: blue;
    --color-link-light: cyan;

    // Typography
    --font-family-basic: 'Roboto Mono', monospace;
    --font-size-basic: 1.5rem;
    --font-weight-basic: 400;
    --font-weight-title: 600;

    --line-height-basic: 1.4;
    --line-height-title: 1.2;

    // Durations
    --transition-duration-basic: .2s;

    // Sizes
    --indent-basic: 1.6rem;

    --year-height: 3.6rem;
    --genre-width: var(--year-height);

    --bar-height: 4.8rem;

    // Cells
    --cell-padding: calc(var(--indent-basic) / 2);
    --cell-background-color-dark: #0a0a0a;
    --cell-background-color-light: #fafafa;

    // Games
    --game-release-unknown-background-color-dark: #0e0e0e;
    --game-release-unknown-background-color-light: #efefef;
}

*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html,
body {
    height: 100%;
}

html {
    font-size: var(--font-size-root, 62.5%);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body {
    background-color: var(--color-lightest);
    color: var(--color-dark);
    font-family: var(--font-family-basic, monospace);
    font-weight: var(--font-weight-basic);
    font-size: var(--font-size-basic);
    line-height: var(--line-height-basic);
}

// Links
a {
    color: var(--color-link-dark);
    transition: all var(--transition-duration-basic);

    &:hover,
    &.active {
        text-decoration: none;
    }

    &.active {
        cursor: default;
    }
}

// Headings
h1,
h2 {
    font-weight: var(--font-weight-title);
    line-height: var(--line-height-title);
}

h1 {
    margin: 0;
}

h6 {
    font-size: var(--font-size-basic);
    line-height: var(--line-height-basic);
}

// Lists
ol,
ul {
    padding: 0;
    list-style: none;
}

// Small
small {
    font-size: 1rem;
}
